<template>
  <div id="space_note">
    <head-top title="笔记">
      <router-link slot="right" to="/space/noteEdit">
        <i class="iconfont icon-add head-right"></i>
      </router-link>
    </head-top>

    <div class="search-input">
      <van-search placeholder="请输入搜索关键词" v-model="searchText" @search="onSearch">
      </van-search>
    </div>

    <van-pull-refresh v-model="isLoading" @refresh="onSearch" class="pull_fresh">
      <van-list v-model="isLoading" :finished="finished" finished-text="没有更多了" :error.sync="error"
        error-text="请求失败，点击重新加载" @load="getDataList">
        <note-list v-if="noteDataList && noteDataList.length > 0" :data-list="noteDataList"
          @select="handleSelectNote">
        </note-list>
      </van-list>
    </van-pull-refresh>

  </div>
</template>

<script>
import NoteList from "@/components/r-note";
import { getNowDate, getSetDate } from "@/utils/index";
export default {
  name: "",
  components: { NoteList },
  data() {
    return {
      isLoading: false,
      noteDataList: [],
      startDate: getSetDate("date", -365 * 10),
      endDate: getNowDate(),
      searchText: "",
      value: "",

      courseOptions: [],

      error: false,
      finished: false,
      pno: 0,
      pageSize: 5,
      total: 0
    };
  },
  computed: {},
  mounted() {
    this.$nextTick(() => {
      this.dataList = [];
    });
  },
  methods: {
    onSearch() {
      this.error = false;
      this.finished = false;
      this.pno = 0;
      this.total = 0;
      // this.dataList = [];
      this.noteDataList = [];
      this.getDataList();
    },
    getDataList() {
      this.isLoading = true;
      if (this.pno === 0 || this.total > this.pno * this.pageSize) {
        this.pno++;
      } else {
        this.isLoading = false;
        this.finished = true;
        return;
      }
      let reqData = {
        startDate: this.startDate,
        endDate: this.endDate,
        psize: this.pageSize,
        pno: this.pno,
        snid: "",
        stuid: this.$store.getters.studentID,
        estate: 1,
        key: this.searchText
      };

      this.$mainHttp.note
        .getSearchList(reqData)
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.sort((a, b) => {
              if (a.Key["CreateTime"] > b.Key["CreateTime"]) {
                return -1;
              } else {
                return 1;
              }
            });
            res.Datas.forEach(item => {
              // this.dataList.push(item);
              this.noteDataList.push({
                id: item.Key["SNID"],
                title: item.Key["SNTtile"],
                content: item.Key["SNContent"],
                state: item.Key["State"],
                createTime: item.Key["CreateTime"],
                CHID: item.Key["CHID"],
                CIID: item.Key["CIID"],
                CHName: item["CHName"],
                CourseName: item["CourseName"]
              });
            });
          }
          if (res.RowsCount) this.total = res.RowsCount;
          if (this.total <= this.pno * this.pageSize) this.finished = true;
          this.isLoading = false;
        })
        .catch(err => {
          this.error = true;
          this.isLoading = false;
        });
    },
    handleSelectNote(item) {
      this.$router.push({
        path: "/space/noteView",
        query: {
          snID: item.id,
          courseID: item["CIID"],
          courseName: item["CourseName"],
          classHourID: item["CHID"],
          classHourName: item["CHName"],
          title: item.title,
          content: item.content,
          createTime: item.createTime
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
@font-title: 16px;
@font-sub-title: 14px;
@font-info: 12px;
#space_note {
  .head_btn {
    display: flex;
    justify-content: flex-end;
  }
  .icon-add {
    font-size: @font-title * 1.4;
  }

  .search-input {
    position: fixed;
    width: 100%;
    top: 44px;
    z-index: 9;
    /deep/ .van-search {
      position: relative;
      &:after {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1PX;
        background-color: #ccc;
        transform: scaleY(0.3);
      }
    }
  }

  .pull_fresh {
    padding-top: 44px;
    background: #fff;
  }
}
</style>



